
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
data () {
return {
show: false,
orderNo: '',
payStatus: false
}
},
methods: {
_getResult () {
this.$tupHttp(this, 'front/account/charge/online/' + this.orderNo).then(res => {
this.payStatus = res.data.model.payStatus === 1
})
}
},
created () {
let u = navigator.userAgent
let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
if (!isAndroid && !isIOS) {
this.show = true
}
this.orderNo = this.$route.query.orderNo
this._getResult()
},
components: {
XHeader
}
}



